* {
  box-sizing: border-box;
}

body {
  background: url("background.5feb226b.jpg") center / cover no-repeat fixed;
}

#wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

#game {
  background: #000;
  display: inline-grid;
  position: relative;
}

.game-status {
  width: 100%;
  height: 50px;
  color: #fff;
  background: #000;
  border: 5px solid #00008b;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

#score {
  width: 200px;
  height: 50px;
  color: #fff;
  background: #000;
  border: 5px solid #00008b;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2rem;
  display: flex;
}

@media screen and (min-width: 1200px) {
  #score {
    margin-top: 20px;
  }
}

#start-button {
  width: 120px;
  height: 40px;
  color: #fff;
  background: #00008b;
  border: 3px solid #000;
  border-radius: 30px;
  outline: none;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

@media screen and (min-width: 1200px) {
  #start-button {
    margin-top: 20px;
  }
}

#mobile-buttons-wrapper {
  width: 170px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  display: flex;
}

@media screen and (min-width: 1200px) {
  #mobile-buttons-wrapper {
    display: none;
  }
}

#left-right-buttons-wrapper {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

#up-button, #left-button, #right-button, #down-button {
  width: 60px;
  height: 60px;
  color: #000;
  background: #ff4500;
  border: none;
  border-radius: 50px;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 1px 1px 4px #555;
}

.hide {
  display: none;
}

.square {
  display: block;
}

.blank {
  background: #000;
}

.wall {
  background-image: radial-gradient(#00f, #00c, #009, #006);
}

.dot {
  background: #daa520;
  border: 8px solid #000;
  border-radius: 50%;
}

.pill {
  background: violet;
  border: 5px solid #000;
  border-radius: 50%;
  animation: pulse 1s linear infinite;
}

@keyframes pulse {
  from {
    transform: scale(.5);
  }

  to {
    transform: scale(1);
  }
}

.ghost {
  border: none;
  border-radius: 10px 10px 2px 2px;
  position: relative;
}

.ghost:before {
  content: "";
  width: 3px;
  height: 3px;
  background: #000;
  border: 2px solid #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 5px;
  left: 3px;
}

.ghost:after {
  content: "";
  width: 3px;
  height: 3px;
  background: #000;
  border: 2px solid #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 5px;
  right: 3px;
}

.scared {
  background: #00f !important;
}

.blinky {
  background: red;
}

.pinky {
  background: pink;
}

.inky {
  background: #0ff;
}

.clyde {
  background: orange;
}

.pacman {
  -webkit-clip-path: polygon(0 0, 20px 0, 20px -5px, 8px 10px, 20px 25px, 20px 20px, 0 20px);
  clip-path: polygon(0 0, 20px 0, 20px -5px, 8px 10px, 20px 25px, 20px 20px, 0 20px);
  background: #ff0;
  border: none;
  border-radius: 50%;
  animation: chew .4s linear infinite forwards;
}

@keyframes chew {
  50% {
    -webkit-clip-path: polygon(0 0, 20px 0, 20px 10px, 8px 10px, 20px 10px, 20px 20px, 0 20px);
    clip-path: polygon(0 0, 20px 0, 20px 10px, 8px 10px, 20px 10px, 20px 20px, 0 20px);
  }
}

